export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "enter-tpn": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Please enter SMS TAN sent to "])},
        "error": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Error"])},
        "error-text": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Field cannot be empty"])},
        "global-error": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Invalid TPN"])}
      },
      "gr": {
        "enter-tpn": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Bitte geben Sie SMS TAN gesendet an ein"])},
        "error": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Fehler"])},
        "error-text": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Feld darf nicht leer sein"])},
        "global-error": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Ungültige TPN"])}
      }
    }
  })
}
